import React, { Fragment } from 'react';
import Helmet from 'react-helmet';
import { navigateTo } from 'gatsby-link';
import PropTypes from 'prop-types';
import axios from 'axios';
import Frame from '../../components/Global/Frame';
import firebase from '../../components/firebase';

import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';
import Button from 'material-ui/Button';
import TextField from 'material-ui/TextField';
import List, { ListItem, ListItemIcon, ListItemText } from 'material-ui/List';

import AccountBoxIcon from 'material-ui-icons/AccountBox';
import PhoneIcon from 'material-ui-icons/Phone';
import EmailIcon from 'material-ui-icons/Email';

const styles = theme => ({
  root: {
    maxWidth: 500,
    margin: '0 auto',
    marginTop: 25,
  },
  action: {
    marginTop: 10,
  },
});

class ContactMe extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      name: '',
      data: [],
      order: 'dibaca',
    };
  }
  componentWillMount() {
    this.getData();
  }

  handleChange = name => event => {
    this.setState({
      [name]: event.target.value,
    });
  };

  getData() {
    firebase.collection('message').get().then(querySnapshot => {
      this.setState({ data: querySnapshot.docs.map(n => n.data()) });
    });
  }

  render() {
    const { classes } = this.props;
    const { name, data } = this.state;
    console.log('snp', data);
    return (
      <Fragment>
        <Helmet defaultTitle="contact me">
          <title>contact me</title>
          <html lang="en" />
        </Helmet>
        <Frame>
          <div className={classes.root}>
            <List style={{ background: '#FFF', borderRadius: 8 }} component="nav">
              {data && data.map((n, i) => (
                <ListItem button key={i}>
                  <ListItemText primary={n.name} />
                </ListItem>
              ))}
            </List>
            <div className={classes.action}>
              <Button variant="raised" color="primary">
                Primary
              </Button>
            </div>
          </div>
        </Frame>
      </Fragment>
    );
  }
}

ContactMe.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(ContactMe);



